.app {
  text-align: center;
}

#bg {
  position: fixed;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center 30%;
  opacity: 0.8;
}

.banner {
  align-items: center;
  background-color: #eec901;
  background-repeat: no-repeat;
  background-size: auto 100%;
  display: flex;
  height: 50px;
  justify-content: flex-end;
  margin-bottom: 5px;
  width: 100%;
}

.banner .btn {
  background: none;
  border: none;
  color: black;
  font-size: 30px;
  line-height: 30px;
  height: 30px;
  margin-right: 5px;
  padding: 0;
  width: 30px;
}

.banner .btn .glyphicon {
  position: static;
}

.main {
  border: none;
  height: calc(100vh - 60px);
  overflow: hidden scroll;
}

.main,
.main .panel {
  background: none;
}

.nameko-panel {
  background: none;
}

/* override Button */
.btn:focus {
  outline: none !important;
}

/* override Panel */

.panel {
  margin: 0 5px 5px;
}

.panel:first-child {
  margin-top: 5px;
}

.panel-heading {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.panel-body {
  padding: 5px;
}

/* override Modal */

.modal {
  display: flex !important;
}

.modal-dialog {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  overflow: auto;
  margin: auto;
  max-height: 90%;
  max-width: 90%;
}

.modal-content {
  display: flex;
  flex-direction: column;
}

.modal-body {
  overflow-x: hidden;
  overflow-y: scroll;
  padding: 10px;
  text-align: center;
}

.modal-footer {
  text-align: center;
}

/* Tunnel & Tunnel Modal */

.tunnel-banner .panel-heading {
  background-size: cover;
  background-position: center;
}

.tunnel-banner.selectable > .panel-heading:hover,
.tunnel > .panel-heading:hover {
  cursor: pointer;
}

.tunnel-banner > * > .panel-body {
  padding-left: 20%;
}

.tunnel-banner .label,
.tunnel .label {
  background-color: rgba(0,0,0,0.5);
  font-size: 18px;
  overflow: auto;
  padding: .2em .2em .3em;
  text-overflow: ellipsis;
}

.tunnel-banner .label.check,
.tunnel .label.check {
  color: green;
  display: inline-block;
  height: 28px;
  width: 28px;
}

.memo {
  margin-top: 1px;
  border: 1px solid black;
}

/* Nameko */

.n-test .nameko {
  /*background-color: '#808834';*/
  background-color: rgba(128, 136, 52, 0.75);
  border-color: #f9f1d9 !important;
}

.n-test .modal-body {
  background-color: #5a391e;
}

.all-block {
  margin-left: -5px;
  margin-right: -5px;
}

.simple-nameko {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
}

.simple-nameko .img {
  max-height: 40px;
  max-width: 100%;
}

.simple-nameko .name {
  background-color: rgba(0,0,0,0.5);
  border-radius: 3px;
  color: white;
  padding: 1px 5px;
  position: relative;
}

.nameko-wrapper {
  padding: 0;
}

.nameko {
  border-radius: 4px;
  border-color: #ccc;
  border: 1px solid #ccc;
  margin: 2px;
  padding: 2px;
}

.nameko.selected {
  border: 2px solid lightblue;
  padding: 1px;
}

.nameko.clickable:active,
.nameko.clickable:focus,
.nameko.clickable:hover {
  background-color: #e6e6e6;
}

.nameko .top-ctn {
  display: flex;
  height: 96px;
}
@media(min-width:768px) {
  .nameko .top-ctn {
    height: 120px;
  }
}

.nameko .simple-nameko {
  flex: 3;
}

.nameko .simple-nameko .img {
  max-height: 96px;
}
@media(min-width:768px) {
  .nameko .simple-nameko .img {
    max-height: 120px;
  }
}

.nameko .simple-nameko .name {
  bottom: 10px;
  padding: 2px 10px;
  position: absolute;
}

.skill-ctn {
  display: flex;
  flex-direction: column;
  width: 18px;
}
@media(min-width:768px) {
  .skill-ctn {
    width: 24px;
  }
}

.skill-ctn .skill {
  display: block;
}

.skill {
  background-color: transparent;
  background-position: center;
  background-size: cover;
  border: none;
  display: inline-block;
  vertical-align: middle;
  height: 18px;
  width: 18px;
}
@media(min-width:768px) {
  .skill {
    height: 24px;
    width: 24px;
  }
}

.skill.unselected {
  filter: grayscale(100%);
  opacity: 0.5;
}

.value-ctn {
  background-color: rgba(0,0,0,0.5);
  color: white;
  display: flex;
  justify-content: space-around;
}

.value {
  background-position: 1px center;
  background-repeat: no-repeat;
  background-size: 11px;
  padding-left: 12px;
  padding-right: 1px;
}
@media(min-width:768px) {
  .value-ctn {
    line-height: 24px;
  }

  .value-ctn .value {
    background-position: 2px center;
    background-size: 22px;
    padding-left: 24px;
    padding-right: 2px;
  }
}

.value-area {
  display: flex;
  justify-content: center;
}

.value-area > div {
  display: flex;
  flex-direction: column;
  margin: 0 5px;
}

.value-area .btn {
  background-color: lightgray;
  border-width: 0;
  border-radius: 50%;
  height: 44px;
  padding: 0;
  width: 44px;
}

.nameko .btn .glyphicon {
  margin-right: 3px;
}

/* Nameko Modal */

.nameko-modal .modal-body {
  overflow: hidden;
  padding: 0;
}

.list {
  height: calc(100vh - 200px);
  overflow: hidden auto;
  padding: 10px;
  width: 100%;
}

.editor {
  background-color: rgba(0,0,0,0.8);
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}

/* Result Panel */

.success,
.fail {
  height: 36px;
  width: 36px;
}

.reward {
  background-size: 36px;
  background-position: 2px center;
  background-repeat: no-repeat;
  display: inline-block;
  padding: 0 2px;
}

.reward img {
  height: 54px;
  width: 54px;
  margin: -9px;
}

.reward span.treasure {
  color: blue;
}

.reward span.item {
  color: #f44336;
}

.result-panel {
  text-align: left;
}

.result-panel .panel {
  padding: 2px;
}

.result-panel h1,
.result-group {
  position: sticky;
  margin: 0;
}

.result-panel h1 {
  color: #f9f1d9;
  background-color: #5a391e;
  font-size: 18px;
  line-height: 30px;
  overflow: hidden;
  padding: 3px 5px;
  text-overflow: ellipsis;
  top: 0;
  white-space: nowrap;
  z-index: 2;
}

.result-group {
  background-color: #f9f1d9;
  border: 5px solid #5a391e;
  color: #5a391e;
  top: 36px;
  z-index: 1;
  margin-bottom: 5px;
  padding-left: 3px;
}

.result.panel {
  background-color: rgba(249, 241, 217, 0.7);
}

.result.panel:first-child {
  margin-top: 5px;
}

.result .fail {
  margin-right: -18px;
}

.result .namekos {
  padding-bottom: 2px;
}

.result .namekos > * {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
}

.result .simple-nameko {
  margin: 0 2px;
}

.result .simple-nameko .name {
  font-size: 12px;
}

.result .skill {
  height: 36px;
  width: 36px;
}

/* Run */

.footer-placeholder {
  height: 69px;
}

.footer {
  bottom: 20px;
  left: 50%;
  position: fixed;
  transform: translate(-50%,0);
  width: fit-content;
  z-index: 100;
}

.footer > * {
  margin: 3px 5px 0 5px;
}

.footer .btn {
  box-shadow: 1px 1px 2px 2px rgba(0,0,0,0.3);
}

#sort .glyphicon {
  margin-left: 6px;
}

#sort .caret {
  display: none;
}

#run .glyphicon {
  margin-right: 6px;
}
